@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,300&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;1,600&display=swap');
.header {
    padding: 1rem 0;
    background-color: #000000;
    color: #fff;
    overflow: hidden;
    z-index: 999;
    position: sticky;
    width: 100%;
    transition: all 0.9s ease;
    top: 0;



    &__inner {
        max-width: 85%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        min-width: 15%;
        // margin-right: 2rem;
        font-size: 8rem;

        a {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #E04141;
            padding: 0.3rem 2rem;
            background-color: #fff;
            border-radius: 8px;
            text-decoration: none;
            font-size: 1rem;
            font-weight: 700;
            text-transform: uppercase;

        }
    }

    &__nav {
        max-width: 75%;
        width: 100%;
    
    }

    &__menu {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: flex-end;
        
        li {
            margin: 0 1rem;
        }

        a {
            color: #fff;
            font-size: 1.1rem;
            text-decoration: none;
            text-transform: uppercase;
            border-bottom: 2px solid transparent;
            transition: 0.3s;
        }
    }

    &__link {
        border: 1px solid black;
    }

    .logo1 {
        font-size: 1.5rem;
        font-weight: 700;
    }
}